<template>
  <div class="col-md-12" style="padding-top:50px; margin-bottom:300px">
    <div>
    <h4> Halaman Login </h4>
    <hr>
      <div class="form-group">
          <div v-if="error" class="alert alert-danger" role="alert">
            Username / Password salah
          </div>
        </div>


        <div class="form-group">
          <div v-if="loading" class="alert alert-primary" role="alert">
            Loading..
          </div>
        </div>

      <Form @submit="handleLogin" :validation-schema="schema">
        <div class="form-group">
          <label for="username">Username / NIM</label>
          <Field name="username" type="text" class="form-control" />
          <ErrorMessage name="username" class="error-feedback" />
        </div>
        <div class="form-group">
          <label for="password">Password</label>
          <Field name="password" type="password" class="form-control" />
          <ErrorMessage name="password" class="error-feedback" />
        </div>

        <div class="form-group">
          <button class="btn btn-success btn-block" :disabled="loading">
            <span
              v-show="loading"
              class="spinner-border spinner-border-sm"></span>
            <span>Login</span>
          </button>
        </div>
   
        <router-link to='/lupa-password' class="nav-link active">
            Lupa Password??
        </router-link>      
        
      </Form>
    </div>
  </div>
 
</template>

<script>
import { Form, Field, ErrorMessage } from "vee-validate";
import * as yup from "yup";

export default {
  name: "Login",
  components: {
    Form,
    Field,
    ErrorMessage,
  },
  data() {
    const schema = yup.object().shape({
      username: yup.string().required("Username is required!"),
      password: yup.string().required("Password is required!"),
    });

    return {
      loading: false,
      error:false,
      message: "",
      schema,
    };
  },
  computed: {
    loggedIn() {
      return this.$store.state.auth.status.loggedIn;
    },
  },
  created() {
    if (this.loggedIn) {
      this.$router.push("/");
    }
  },
  methods: {
    handleLogin(user) {
      this.loading = true;

      this.$store.dispatch("auth/login", user).then(
        () => {
          this.$router.push("/");
        },
        (error) => {
          this.loading = false;
          this.error = true;  
          this.message =
            (error.response &&
              error.response.data &&
              error.response.data.message) ||
            error.message ||
            error.toString();
        }
      );
    },
  },
};
</script>